.main-header{
    text-align: center;
    margin: 10px;
    font-size: 24pt;
}

.learn-tab {
    background-color: transparent;
    color: lightblue;
    width: 33%;
    font-size: 16pt;
    border: 1px solid darkslategray;
    border-top: 0px;
    transition: all .2s ease-in-out;
}

.learn-tab:hover {
    color: white;
    text-shadow: 0px 0px 5px white;
}

.tab-container {
    margin: 0px 20%;
}