.stock {
    text-align: center;
    pointer-events: none;
}
.header {
    text-align: center;
    margin-top: 10px;
    font-size: 16pt;
}
.search {
    margin: 10px 20%;
}
.search-bar {
    background-color: black;
    border-width: 0px;
    border-bottom: 1px solid lightblue;
    margin-right: 10px;
    color: lightblue;
}
.search-button {
    background-color: slateblue;
    color: white;
    border-radius: 20px;
    border: 0px;
    padding: 8px;
    margin: 10px;
    width: 100px;
    transition: all .2s ease-in-out;
}
.search-button:hover {
    background-color: lightblue;
    color: black;
    transform: scale(1.1);
}
.search-suggestions {
    background-color: white;
    color: black;
    width: 300px;
    border-radius: 10px;
    position: absolute;
}
.suggestion {
    padding: 5px;
    border-radius: 10px;
    transition: all .2s ease-in-out;
    width: 100%;
    text-align: left;
    border: 0px;
    background-color: white;
}
.suggestion:hover {
    background-color: grey;
    color: white;
}
.main-info {
    margin: 10px 20%;
    background-color: rgb(30,30,30);
    padding: 10px;
    border-radius: 20px;
    color: lightblue;
}
.info-header {
    display: flex;
    justify-content: space-around;
}
.info-header-item {
    align-self: flex-end;
    font-size: 20pt;
    margin: 0 2px;
}
.info-header-item a {
    font-size: 12pt;
}
.stock-name {
    font-size: 32pt;
    font-weight: bold;
}
.center {
    text-align: center;
}
.description {
    margin: 10px 20%;
    color: white;
}
.fundamentals{
    margin: 10px 20%;
    background-color: rgb(50,50,50);
    color: lightblue;
    padding: 10px;
    border-radius: 10px;
}
.general-info{
    margin: 10px 20%;
    background-color: rgb(30,30,30);
    color: lightblue;
    padding: 10px;
    border-radius: 10px;
}
/* Tooltip container */
.tooltip {
    position: relative;
    display: inline-block;
    border-bottom: 1px dotted black; /* If you want dots under the hoverable text */
}
  
  /* Tooltip text */
.tooltip .tooltiptext {
    visibility: hidden;
    width: 400px;
    background-color: rgba(30,30,30,50);
    color: white;
    text-align: center;
    border-radius: 6px;
    padding: 10px;
    top: -5px;
    left: 105%;
    position: absolute;
    z-index: 1;
    font-size: 10pt;
}
  
  /* Show the tooltip text when you mouse over the tooltip container */
.tooltip:hover .tooltiptext {
    visibility: visible;
}

.stock-community {
    margin: 10px 20%;
    background-color: rgb(50,50,50);
    color: lightblue;
    padding: 10px;
    border-radius: 10px;
}

.community-header{
    display: flex;
    justify-content: space-around;
}

.community-header button{
    background-color: rgb(50,50,50);
    border: 0px;
    color: lightblue;
    font-weight: bold;
    transition: all .2s ease-in-out;
}
.community-header button:hover {
    color: white;
    text-shadow: 0px 0px 5px white;
    transform: scale(1.1);
}
.time-scale{
    margin-top: -50px;
    margin-bottom: 20px;
    text-align: center;
}

.time-scale button {
    background-color: slateblue;
    color: white;
    border-radius: 15px;
    border: 0px;
    margin: 0 10px;
    height: 30px;
    width: 30px;

    transition: all .2s ease-in-out ;
}

.time-scale button:hover {
    background-color: lightblue;
    color: black;
    transform: scale(1.1);
}

.sec-header {
    background-color: rgb(20,20,20);
    text-align: center;
    padding: 4px;
    border-radius: 10px;
    font-weight: bold;
    margin-bottom: 10px;
}

.sec-content {
    display: flex;
}

.sec-content img {
    margin-right: 20px;
    border-radius: 10px;
}

.fundamental-container {
    display: flex;
}

.fundamental-container div {
    margin: 0 20px;
}

.fundamental-subheader {
    color: lightgray;
}

.comment {
    background-color: rgb(30,30,30);
    margin-bottom: 10px;
    padding: 7px;
    border-radius: 10px;
}

.comment hr {
    color: lightblue;
    margin-bottom: 0px;
}

.comment-footer {
    font-size: 10pt;
    display: flex;
    justify-content: space-between;
}

.comment-bar {
    background-color: transparent;
    border: 1px solid lightblue;
    margin-right: 10px;
    margin-bottom: -10px;
    color: lightblue;
    width: 50%;
}