.guide {
    background-color: rgb(50,50,50);
    margin: 10px 20%;
    padding: 10px;
    color: lightblue;
    border-radius: 20px;
}
.welcome {
    text-align: center;
    font-size: 20pt;
    padding: 100px 20%;
    margin-bottom: 10px;
    position: relative;
    text-shadow: 3px 3px 3px black;
}
.welcome::before {
    content: "";
    position: absolute;
    top: 0; left: 0;
    width: 100%; height: 100%;
    background-image: url(https://thumbs.gfycat.com/ThickAdmirableHackee-size_restricted.gif);
    background-size: cover;
    background-position: center;
    filter: brightness(30%);
  }
.welcome-text {
    position: relative;
}
.guide-button {
    background-color: slateblue;
    color: white;
    border-radius: 20px;
    border: 0px;
    padding: 10px;
    margin: 10px;
    width: 100px;
    transition: all .2s ease-in-out;
}
.guide-button:hover {
    background-color: lightblue;
    color: black;
    transform: scale(1.1);
}